import React, { Component } from "react";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import BSCSwapAgentImpl from "./contracts/BSCSwapAgentImpl.json";
import ETHSwapAgentImpl from "./contracts/ETHSwapAgentImpl.json";
import IERC20 from "./contracts/IERC20.json";
import Web3 from "web3";
import utils from "web3-utils";
import assert from "assert";

const RINKEBY = {
  CHAIN_ID: 4,
  TOKEN_ADDRESS: "0x6bd5eedAE4dF424262fb76a785E7Dc231b3EdaA1",
  SWAP_AGENT_IMPL: ETHSwapAgentImpl,
};

const BSC_TESTNET = {
  CHAIN_ID: 97,
  TOKEN_ADDRESS: "0xFBd4637305BB9aF06184279a4612BD4440a03b2c",
  SWAP_AGENT_IMPL: BSCSwapAgentImpl,
};

class App extends Component {
  state = {
    loaded: false,
    targetChain: null,
    swapFee: null,
    amount: 0,
    registered: false,
    tokenBalance: 0,
    erc20SpendingApproved: 0,
    connectedWallet: null,
    buttonText: "Connect",
  };

  seenEvents = {};

  componentDidMount = async () => {
    try {
      // Is there an equivalent for web3??
      if (window.ethereum) {
        window.ethereum.on("accountsChanged", () => {
          console.log("Account changed!");
          if (this.web3) {
            this.setContractAddresses(parseInt(window.ethereum.chainId));
          }
        });
        window.ethereum.on("chainChanged", (chainId) => {
          console.log("New chain! ", chainId);
          if (this.web3) {
            this.setContractAddresses(parseInt(chainId));
          }
        });
      }
      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ loaded: true });
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Make sure you are on the rinkeby network. Check console for details.`
      );
      console.error(error);
    }
  };

  setContractAddresses = async (chainId) => {
    console.log("setcontract");
    assert(typeof chainId == "number");

    this.web3 = new Web3(window.ethereum);
    this.accounts = await this.web3.eth.getAccounts();

    let targetChain = null;
    let registered = null;

    if (chainId == RINKEBY.CHAIN_ID) targetChain = RINKEBY;
    else if (chainId == BSC_TESTNET.CHAIN_ID) targetChain = BSC_TESTNET;
    else assert(false);

    this.swapAgent = new this.web3.eth.Contract(
      targetChain.SWAP_AGENT_IMPL.abi,
      targetChain.SWAP_AGENT_IMPL.networks[chainId].address
    );

    this.token = new this.web3.eth.Contract(
      IERC20.abi,
      targetChain.TOKEN_ADDRESS
    );

    const swapFee = await this.swapAgent.methods.swapFee().call();

    if (targetChain == RINKEBY)
      registered = await this.swapAgent.methods
        .registeredERC20(targetChain.TOKEN_ADDRESS)
        .call();

    this.setState({
      targetChain: targetChain,
      swapFee: swapFee,
      registered: registered,
      buttonText: "Connected " + window.ethereum.selectedAddress,
      connectedWallet: window.ethereum.selectedAddress,
    });

    this.updateApprovedAmount();
    this.listenToSwapEvents();
  };

  handleConnect = async () => {
    if (this.state.buttonText != "Connect") return;

    window.ethereum
      .request({ method: "eth_requestAccounts" })
      .catch((error) => {
        if (error.code === 4001) {
          // EIP-1193 userRejectedRequest error
          console.log("Please connect to MetaMask.");
        } else {
          console.error(error);
        }
      })
      .then((result) => {
        if (result) {
          const chainId = parseInt(window.ethereum.chainId);

          if (chainId != RINKEBY.CHAIN_ID && chainId != BSC_TESTNET.CHAIN_ID) {
            alert(
              "We currently only support the Rinkeby testnet and the BSC testnet. Please change your network in metamask"
            );
            // How to handle disconnect?
            return;
          }

          this.setContractAddresses(chainId);
        }
      });
  };

  handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  };

  handleSwitch = async () => {
    if (!this.web3) {
      alert("Connect a wallet first!");
      return;
    }

    const chainId = parseInt(window.ethereum.chainId);

    if (chainId == RINKEBY.CHAIN_ID) {
      const params = [
        {
          chainId: String("0x" + BSC_TESTNET.CHAIN_ID.toString(16)),
          chainName: "Binance Testnet",
          nativeCurrency: {
            name: "BNB",
            symbol: "BNB",
            decimals: 18,
          },
          rpcUrls: ["https://data-seed-prebsc-1-s3.binance.org:8545"],
          blockExplorerUrls: ["https://testnet.bscscan.com/"],
        },
      ];

      window.ethereum.request({
        method: "wallet_addEthereumChain",
        params: params,
      });
    } else {
      alert(
        "Please switch chain manually on Metamask as Metamask does not allow switching to default chains programatically."
      );
      return;
    }

    this.setContractAddresses(chainId);
  };

  handleSending = async () => {
    if (!this.web3) {
      alert("Connect a wallet first!");
      return;
    }

    const amountBN = utils.toBN(this.state.amount);
    const amountInWei = utils.toWei(amountBN);

    if (this.state.targetChain == RINKEBY) {
      await this.swapAgent.methods
        .swapETH2BSC(
          this.state.targetChain.TOKEN_ADDRESS,
          amountInWei.toString()
        )
        .send({ from: this.accounts[0], value: this.state.swapFee });
    } else if (this.state.targetChain == BSC_TESTNET) {
      await this.swapAgent.methods
        .swapBSC2ETH(
          this.state.targetChain.TOKEN_ADDRESS,
          amountInWei.toString()
        )
        .send({ from: this.accounts[0], value: this.state.swapFee });
    } else {
      assert(false);
    }
  };

  updateApprovedAmount = async () => {
    const spendingApproved = utils.fromWei(
      String(
        await this.token.methods
          .allowance(this.accounts[0], this.swapAgent._address)
          .call()
      )
    );

    const tokenBalance = utils.fromWei(
      await this.token.methods.balanceOf(this.accounts[0]).call()
    );

    this.setState({
      erc20SpendingApproved: spendingApproved,
      tokenBalance: tokenBalance,
    });
  };

  handleSwapEvent = (object) => {
    if (object["id"] in this.seenEvents) return;

    alert(object["event"]);

    if (object["event"] == "Approval") {
      this.updateApprovedAmount();
    }

    this.seenEvents[object["id"]] = true;
  };

  handleApprove = async () => {
    if (!this.web3) {
      alert("Connect a wallet first!");
      return;
    }

    const amountBN = utils.toBN(this.state.amount);
    const amountInWei = utils.toWei(amountBN);

    await this.token.methods
      .approve(this.swapAgent._address, amountInWei.toString())
      .send({ from: this.accounts[0] });
  };

  listenToSwapEvents = async () => {
    console.log("listenToSwapEvents");
    this.token.events
      .Approval({ owner: this.accounts[0] })
      .on("data", this.handleSwapEvent);
    this.swapAgent.events
      .SwapStarted({ fromAddr: this.accounts[0] })
      .on("data", this.handleSwapEvent);
    this.swapAgent.events
      .SwapFilled({ toAddress: this.accounts[0] })
      .on("data", this.handleSwapEvent);
  };

  render() {
    if (!this.state.loaded) {
      return <div>Loading Web3, accounts, and contract...</div>;
    }
    return (
      <Container>
        <Row>
          <Col md={7}>
            <h1>ERC20 &#11138; BEP20 Swap Service</h1>
            <p>
              Use this service to bridge your ERC20 token to and from the
              Rinkeby Test Network onto the Binance Smart Chain (BSC) Testnet
            </p>
          </Col>
          <Col md={1}>
            <Button onClick={this.handleConnect}>
              {this.state.buttonText}
            </Button>
          </Col>
        </Row>

        <Row>
          <Col md={4}>
            <h2>
              Current {this.state.targetChain == RINKEBY ? "ERC20" : "BEP20"}{" "}
              Token
            </h2>
            <p>
              Symbol: ASD deployed at Address:{" "}
              <b>
                {this.state.targetChain && this.state.targetChain.TOKEN_ADDRESS}{" "}
              </b>
            </p>
          </Col>
          <Col md={4}>
            Current Swap Fee: <b>{this.state.swapFee}</b> wei
          </Col>
          <p
            style={this.state.targetChain == RINKEBY ? {} : { display: "none" }}
          >
            registeredERC20 for token address is{" "}
            <b>{this.state.registered ? "true" : "false"} </b>
          </p>
        </Row>

        <Row>
          <Col md={5}>
            <h3>
              Approve swap contract to spend your{" "}
              {this.state.targetChain == RINKEBY ? "ERC20" : "BEP20"}{" "}
            </h3>
            <p>
              From:{" "}
              <b>
                {this.state.targetChain == RINKEBY ? "RINKEBY" : "BSC_TESTNET"}
              </b>{" "}
              network
            </p>
            <p>
              Token Balance: <b>{this.state.tokenBalance}</b>
            </p>
            <p>
              Current approval status/amount:{" "}
              <b>{this.state.erc20SpendingApproved} ASD</b>{" "}
            </p>
            <p>I want to send </p>
            <input
              type="number"
              name="amount"
              onChange={this.handleInputChange}
              value={this.state.amount}
            />
            <Row>
              <Col
                style={
                  Number(this.state.amount) >
                  Number(this.state.erc20SpendingApproved)
                    ? {}
                    : { display: "none" }
                }
              >
                <Button onClick={this.handleApprove}>Approve ASD</Button>
              </Col>
              <Col>
                <Button onClick={this.handleSending}>Swap ASD</Button>{" "}
              </Col>
            </Row>
          </Col>

          <Col md={1}>
            <h3> &#10132; </h3>
            <br />
            <Button onClick={this.handleSwitch}>Switch</Button>
          </Col>

          <Col md={5}>
            <p>
              To{" "}
              <b>
                {this.state.targetChain == RINKEBY ? "BSC_TESTNET" : "RINKEBY"}
              </b>{" "}
              recipient Address: <b>{this.state.connectedWallet}</b>
            </p>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default App;
